@import url('https://fonts.googleapis.com/css2?family=Comfortaa&family=Edu+NSW+ACT+Foundation&family=Sono&display=swap');

.header{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: black;
    color: white;
    width: 100vw;
    height: 10rem;
    line-height: 8px;
    border-bottom: 1px solid white;
}
.header h1{
    color: cornsilk;
}
.header p{
    letter-spacing: 0.5px;
    font-family: 'Edu NSW ACT Foundation', cursive;
}
.header h3 span {
    color: crimson;
    font-weight: bolder;
}