.keyboardButton{
    background-color: white;
    border: 2px solid black;
    border-radius: 5px;
    height: 20px 30px;
    width: 30px;
    cursor: pointer;
    padding: 5px 7px;
    margin: 2px;


}
.keyboardButton:hover{
    background-color: black;
    border: 2px solid white;
    color: white;
    transition: 0.2s;

}