.gameover h2{
    color: white;
}
.gameover h2 span{
    color: crimson;
}
.gameover h3 span{
    color: crimson;
}
.gameover h3{
    color: white;
}
.gameover{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.gameover button{
    padding: 4px 8px;
    border-radius: 5px;
    cursor: pointer;
    border: 1px solid cornsilk;
  }
  .gameover button:hover{
      color: white;
      background-color: black;
      border: 2px solid white;
      transition: 0.2s;
  }