.winner h2{
    color: white;
}
.winner h2 span{
    color: crimson;
}
.winner{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

 .winner button{
  padding: 4px 8px;
  border-radius: 5px;
  cursor: pointer;
  border: 1px solid cornsilk;
}
.winner button:hover{
    color: white;
    background-color: black;
    border: 2px solid white;
    transition: 0.2s;
}